@import url(https://fonts.googleapis.com/css2?family=KoHo&display=swap);
body {
  margin: 0;
  font-family: 'KoHo', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card_item{
    border:none !important;
}
.shadow_effect{
    box-shadow: 0;
    transition: box-shadow 0.9s, opacity 0.5s;
    will-change: transform;
}
.shadow_effect:hover{
    box-shadow: 0px 30px 100px -10px rgba(0, 0, 0, 0.4);
}
.Unordered_list li{
    padding:8px 0;
}
.Unordered_list li:hover{
    color: #20C20E;
    text-transform: uppercase;
    font-weight:550;
}
.shadow_effect{
    box-shadow: 0;
    transition: box-shadow 0.5s, opacity 0.5s;
    will-change: transform;
}
.shadow_effect:hover{
    box-shadow: 0px 30px 100px -10px rgba(0, 0, 0, 0.4);
}
.main_container{
    height:auto;
}
.right{
float:right;
}

.menu_active{
    border-bottom: 2px solid #20C20E ;
    font-weight: bolder;
}
.nav-link:hover{
    color:#20C20E !important;
}
.shadow{
box-shadow: 0px 6px 10px 2px rgba(112,112,112,1);
    background-color: black;
}

.background{
    background-color: black;
    height:auto;
}
.icon{
    color:white;
}
